<template>
    <div>
        <div id="login"></div>
        <div class="login_bg">
            <div id="logo">
                <img src="../assets/img/logo.png" alt=""/>
            </div>
            <div class="form">
                <div class="userName">
                    <span></span>
                    <input type="text" placeholder="" v-model="loginName"/>
                </div>
                <div class="passWord">
                    <span>&nbsp;&nbsp;&nbsp;</span>
                    <input type="password" placeholder="" v-model="loginPwd"/>
                </div>
                <div class="choose_box">
                    <div>
                        <input type="checkbox" checked="checked"/>
                        <span></span>
                    </div>
                    <a></a>
                </div>
                <button class="login_btn" v-on:click='login'>&nbsp;&nbsp;</button>
            </div>
        </div>
    </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
    name: 'Login',
    data(){
        return{
            loginName:'',
            loginPwd:''
        }
    },
    watch:{ //
        loginName:function(newText){
            this.loginName = newText
        },
        loginPwd:function(newText){
            this.loginPwd = newText
        }
    },
    methods:{
        login:function(){
            if(this.loginName == ''){
                Toast({
                    message: '',
                    position: 'middle',
                    duration: 3000
                });
                return false;
            }
            if(this.loginPwd == ''){
                Toast({
                    message: '',
                    position: 'middle',
                    duration: 3000
                });
                return false;
            }
            fetch("/apis/wxWebApp/wechat/userLoginPost.action",{
                method:'POST',
                body:JSON.stringify({ "loginName":this.loginName,"loginPwd":this.loginPwd})
            })
            .then(res => res.json())
            .then(data => {
                //console.log(data);
                //
                if(data.ok){
                    this.$router.push({path: "/home"});
                }else{ //
                    Toast({
                        message: data.retMsg,
                        position: 'middle',
                        duration: 3000
                    });
                }
            })
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "../assets/css/login.css"
</style>



